import { useEffect, useState } from "react";
import axios from "axios";
import { Link } from "react-router-dom";
import { GLOBAL } from "../../constants/constants";
import { UpdateTodo } from "../UpdateToDo/UpdateToDo";
import ToDoLists from "../ToDoLists/ToDoLists";

const DisplayToDo = () => {
  const [toDoData, setToDoData] = useState([]);
  const [update, setUpdate] = useState(false);
  const [id, setId] = useState("");
  const [modal, setModal] = useState(false);

  useEffect(() => {
    axios
      .get(`${GLOBAL.URL}/getToDos`)
      .then((res) => {
        console.log('res data',res)
        setToDoData(res.data.data);
      })
      .catch((e) => {
        console.log(e);
      });
  }, []);

  const updateHandler = () => {
    setUpdate(!update);
  };

  const editHandler = (e) => {
    setId(e.target.name);
    setModal(true);
  };

  const closeHandler = () => {
    setId("");
    setModal(false);
  };

  const deleteHandler = (e) => {
    axios.delete(`${GLOBAL.URL}/deleteToDo${e.target.name}`);
    setToDoData((data) => {
      return data.filter((todo) => todo._id !== e.target.name);
    });
  };

  Object.keys(toDoData).forEach((data, index) => {
    // console.log(toDoData[data]);
  })
  return (
    <section className="todo-container">
      <Link to="/add-todo" className="todo-btn-link">
        <button className="todo-btn">Add ToDo</button>
      </Link>
      <button className="todo-btn">Add New ToDo</button>
      <section className="todo-data">
        <h1>ToDo's</h1>
        <ul className="todo-list">
          {Object.keys(toDoData).forEach((data, index) => {
            console.log(toDoData[data]);
            <ToDoLists key={index} data={toDoData[data]} editHandler={editHandler} deletehandler={deleteHandler} />;
          })}
        </ul>
      </section>
      {modal ? (
        <section className="update-todo">
          <div className="update">
            <p onClick={closeHandler} className="close">&times;</p>
            <UpdateTodo _id={id}
              closeHandler={closeHandler}
              updateHandler={updateHandler} />
          </div>
        </section>
      ) : (
        ""
      )}
    </section>
  );

  


};

export default DisplayToDo;
